@include body-class(true) {
  .cross-sell-pro-banner {
    margin: 30px 0 50px;
    background: linear-gradient(
      107.15deg,
      $red-100 0%,
      $blue-400 49.48%,
      $green-100 76.56%,
      $purple-100 100%
    );
    border-radius: 28px;
    padding: 3px;
    overflow: hidden;

    @include media(max-width, md) {
      margin-bottom: 20px;
    }

    &__inner {
      position: relative;
    }

    &__content {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 15px;
      padding: 30px 15px;
      background: $gray-75;
      border-radius: 28px;
    }

    h2 {
      color: $gray-800;
    }

    h2,
    p {
      text-align: center;
    }

    .cross-sell-pro-banner__emph {
      display: inline-block;
      position: relative;

      &:after {
        content: "";
        position: absolute;
        bottom: -10px;
        width: 100%;
        left: 0;
        height: 12px;
        background: 0 0 / 100% 100% no-repeat
          url("data:image/svg+xml;base64,");
      }

      &:before {
        content: "";
        position: absolute;
        right: 50px;
        top: 55px;
        width: 110px;
        height: 95px;
        background: center / contain no-repeat
          url("data:image/svg+xml;base64,");
        pointer-events: none;

        @include media(max-width, lg) {
          display: none;
        }
      }
    }
  }
}
