@include body-class(true) {
  .cross-sell-plugin-card-description {
    color: $gray-550;
  }

  .cross-sell-card {
    &:hover,
    &:focus {
      border-color: $gray-400;
      box-shadow: 0px 2px 4px 0px rgba($gray-600, 0.1),
        0px 1px 16px 0px rgba($gray-600, 0.05),
        0px 6px 12px 0px rgba($gray-600, 0.08);
    }
  }

  .cross-sell-plugin-card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .cross-sell-plugin-card-content {
    display: flex;
    flex-direction: column;
    gap: 10px;
    min-height: 66px; // 3 lines of text at 22px line-height : .cross-sell-plugin-card-description

    h2 {
      font-weight: 600;
      font-size: 24px;
      line-height: 30px;
      letter-spacing: 0px;
      color: $gray-800;
    }
  }

  .cross-sell-plugin-card-rating {
    display: flex;
    gap: 15px;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
    line-height: 22px;
    letter-spacing: 0px;
    color: $gray-550;

    &__stars {
      display: flex;
      align-items: center;
      gap: 5px;
    }
  }

  .cross-sell-plugin-card-description {
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 0px;
    color: $gray-700;
  }

  .cross-sell-plugin-card-features {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  .cross-sell-plugin-card-feature {
    display: flex;
    gap: 8px;
    align-items: center;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    letter-spacing: 0px;
    color: $gray-550;
  }

  .cross-sell-plugin-card {
    display: grid;
    grid-template-columns: auto 1fr; // Logo takes only needed space, content takes remaining
    gap: 10px;
    align-items: center;

    &__logo {
      width: 40px;
      height: 40px;
      object-fit: contain;
      flex-shrink: 0; // Prevent logo from shrinking in flex layout
    }

    &__content {
      display: flex;
      flex-direction: column;
      gap: 2px;
      min-width: 0; // Allows text to wrap properly
    }

    &__title {
      font-size: 24px;
      font-weight: 700;
      line-height: 1.3;
      margin: 0;
      color: $gray-900;

      @include media(max-width, sm) {
        font-size: 16px;
      }
    }

    &__subtitle {
      font-size: 13px;
      color: $gray-550;
      font-weight: 500;
      line-height: 18px;

      @include media(max-width, sm) {
        font-size: 12px;
      }
    }

    // Responsive: Stack vertically on very small screens
    @include media(max-width, xs) {
      grid-template-columns: 1fr;
      text-align: center;
      gap: 15px;

      &__logo {
        justify-self: center;
      }
    }
  }

  .cross-sell-plugin-card-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;
    padding-top: 15px;

    &__installs {
      font-size: 15px;
      line-height: 22px;
      color: $gray-550;
      flex-shrink: 0; // Prevents text from wrapping
      font-weight: 400;
    }

    &__cta {
      margin-left: auto; // Pushes CTA to the right when installs are present
    }

    // When only CTA is shown (extended_view = false)
    &--cta-only {
      justify-content: flex-start; // Align CTA to the left

      .cross-sell-plugin-card-footer__cta {
        margin-left: 0; // Remove auto margin to align left
      }
    }

    // Responsive: Stack vertically on small screens
    @include media(max-width, sm) {
      flex-direction: column;
      align-items: flex-start;
      gap: 10px;

      &__cta {
        margin-left: 0;
      }

      &--cta-only {
        align-items: flex-start;
      }
    }
  }
}
