@include body-class(true) {
  #wpfooter {
    display: none !important;
  }

  .cross-sell-footer {
    background: $gray-50;
    margin-top: 50px;

    @include media(max-width, md) {
      margin-top: 30px;
    }

    h2,
    p {
      margin: 0;
    }

    &__wrapper {
      display: flex;
      gap: 30px;
      flex-direction: column;
      align-items: center;
      padding: 50px 0;
    }

    &__content {
      display: flex;
      align-items: center;
      gap: 50px;
      max-width: 1080px;

      @include media(max-width, lg) {
        flex-direction: column;
        align-items: flex-start;
        gap: 30px;
      }
    }

    &__title {
      display: flex;
      flex-direction: column;
      gap: 10px;
      flex: 1 0 auto;

      h2 {
        color: $gray-800;
      }

      p {
        font-size: 20px;
        line-height: 30px;
        letter-spacing: -0.5px;
        color: $gray-700;
      }
    }

    &__description {
      p {
        font-weight: 500;
        padding: 20px;

        @include media(max-width, lg) {
          padding: 0;
        }
      }
    }

    &__reviews {
      display: flex;
      flex-wrap: wrap;
      gap: 30px;
      align-items: center;
      justify-content: center;
      padding: 30px 0;
      width: 100%;

      .review {
        display: flex;
        gap: 10px;
        height: 34px;

        &:hover {
          .review__image {
            svg {
              fill: $gray-550;
            }
          }
        }
      }

      .review__rating {
        display: flex;
        gap: 10px;
        align-items: center;
        justify-content: center;
      }

      .review__ratings {
        font-weight: 500;
        font-size: 13px;
        line-height: 22px;
        letter-spacing: 0px;
        color: $gray-700;
      }

      .review__reviews {
        font-weight: 500;
        font-size: 13px;
        line-height: 22px;
        letter-spacing: 0px;
        color: $gray-700;

        &:hover {
          text-decoration: underline;
          color: $gray-700;
        }
      }

      .review__logo {
        display: flex;
      }

      .review__image {
        width: 17px;

        svg {
          fill: $gray-400;
        }

        &--flip {
          -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
        }
      }
    }

    &__credits {
      padding: 30px 0;
      width: 100%;
      text-align: center;
      border-top: 1px solid $black;
    }
  }
}
