@import "./variables.scss";
@import "./shared-ui.scss";
@import "./hero.scss";
@import "./section.scss";
@import "./plugin-card.scss";
@import "./pro-plugins-banner.scss";
@import "./footer.scss";

@include body-class(true) {
  * {
    box-sizing: border-box;
  }

  &.sui-wrap:not(.sui-modal) {
    margin-top: 0;
  }

  h1,
  h2,
  h3 {
    margin: 0;
  }

  .cross-sell-text-center {
    text-align: center;
  }

  p {
    font-size: 16px;
    line-height: 30px;
    letter-spacing: -0.5px;
    margin: 0;

    &:last-of-type:not(:last-child) {
      margin-bottom: 0;
    }
  }

  .wpmudev-cross-sell-page {
    background: $white;
    margin-left: -30px;
    margin-right: -30px;

    @include media(max-width, md) {
      margin-left: -10px;
      margin-right: -10px;
    }
  }

  .cross-sell-wrapper {
    max-width: 1280px;
    padding-left: 50px;
    padding-right: 50px;
    margin-left: auto;
    margin-right: auto;

    @include media(max-width, md) {
      padding-left: 30px;
      padding-right: 30px;
    }
  }

  .cross-sell-link {
    font-weight: 400;
    color: $blue-100;
    text-decoration: underline;

    &:hover:not(.sui-button),
    &:focus:not(.sui-button) {
      color: $blue-300;
    }
  }

  .cross-sell-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px 30px;

    .cross-sell-card {
      &.cross-sell-card--major {
        grid-column: span 2;
      }

      @include media(max-width, lg) {
        grid-column: span 1;
      }

      @include media(max-width, md) {
        grid-column: span 2;
      }
    }

    @include media(max-width, lg) {
      grid-template-columns: repeat(2, 1fr);
    }

    @include media(max-width, md) {
      grid-template-columns: 1fr;
    }
  }

  .cross-sell-card {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 25px;
    border-radius: 10px;
    border: 1px solid $gray-200;
    background: $white;
    box-shadow: 0px 2px 1px 0px rgba($gray-600, 0.05),
      0px 1px 2px 0px rgba($gray-600, 0.05);

    &:hover,
    &:focus {
      border-color: $gray-400;
      box-shadow: 0px 1px 2px 0px rgba($gray-600, 0.05),
        0px 2px 1px 0px rgba($gray-600, 0.05);
    }
  }

  // button
  .cross-sell-button {
    &:not(.sui-button) {
      font-weight: 500;
      font-size: 15px;
      line-height: 24px;
      letter-spacing: 0px;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 8px 16px;
      border-radius: 5px;
      background: $blue-100;
      color: $white;
      cursor: pointer;
      transition: background 0.3s;

      &:hover {
        background: $blue-200;
        color: $white;
      }

      &:focus {
        outline: 2px solid $blue-100;
        outline-offset: 2px;
        background: $blue-300;
        color: $white;
      }
    }
  }

  .cross-sell-highlighted-price {
    display: inline-block;
    margin-left: 4px;
    text-decoration: line-through;
    opacity: 0.5;
  }
}

#wpbody-content {
  padding-bottom: 0 !important;
}
