@include body-class(true) {
  .cross-sell-hero {
    background: linear-gradient(126.64deg, $white 14.27%, $blue-50 234.25%);
    position: relative;

    h2 {
      font-size: 40px;
      line-height: 60px;
      letter-spacing: -0.5px;
      color: $gray-800;

      @include media(max-width, lg) {
        font-size: 32px;
        line-height: 48px;
      }

      @include media(max-width, md) {
        font-size: 28px;
        line-height: 36px;
      }
    }

    p {
      font-weight: 500;
      font-size: 24px;
      line-height: 36px;
      letter-spacing: 0px;

      @include media(max-width, lg) {
        font-size: 20px;
        line-height: 30px;
      }

      @include media(max-width, md) {
        font-size: 16px;
        line-height: 24px;
      }
    }

    &__wrapper {
      background: center / cover no-repeat
        url("data:image/svg+xml;base64,");

      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 20px;
      padding: 100px 0;
      text-align: center;

      @include media(max-width, lg) {
        gap: 10px;
        padding: 80px 0;
      }

      @include media(max-width, sm) {
        padding: 40px 0;
        background-size: contain;
      }
    }

    &__logo {
      display: flex;
      position: absolute;
      bottom: -38px;
      left: 50%;
      transform: translateX(-50%);

      @include media(max-width, lg) {
        bottom: -30px;

        svg {
          width: 60px;
          height: 60px;
        }
      }

      @include media(max-width, sm) {
        bottom: -20px;

        svg {
          width: 40px;
          height: 40px;
        }
      }
    }
  }
}
